import React, { PureComponent, Fragment } from 'react';
import { connect } from 'react-redux'
import { actionCreatore } from './store'
import { Modal, Switch, Button, Stepper, Toast } from 'antd-mobile';
import URL from '../../../../common/Axios/URL_SET'

import {
    Container,
    MyTop,
    MyList,
    MyWarp,
    TopImage,
    TopWarp,
    UserMessage,
    UserPhone,
    UserId,
    UserLevel,
    TopWarpLeft,
    OutLogin,
    ProgressWarp,
    Kefu
} from './style'
import './style.css'

import { DvaCell } from '../../../../common/Template'
const prompt = Modal.prompt;

class My extends PureComponent {
    constructor(props) {
        super(props); 
        this.state = { 
            zfbShow: false
        }
    }
    componentDidMount(){
        this.props.handleInital() 
        console.log(JSON.parse(localStorage.getItem('user_info')))
    }

    popupCloose() {
        this.setState({ zfbShow: true })
    }

    render() { 
        const { newUserInfo, handleKF, newwxList, handleCopy, WeiXinService, centerBDNum, handleBDoK, chnageBDNum, BDNum, BDModal, changeBDModal, handleUser, handleExt, handleNone, history, handleOut, handleApply, isCancel, JFList, JFPage, handleGift, maxBDNum, handleNotice} = this.props
        const Message = newUserInfo.toJS()
        console.log(Message)
        const wxList = newwxList.toJS()
        const alert = Modal.alert;
        const showAlert = () => {
            const alertInstance = alert('退出登录', '您确定要退出吗？', [
                { text: '取消', onPress: () => {  }, style: 'default' },
                { text: '确定', onPress: () => { handleOut(history) } },
            ]);
        };
        return (<Fragment>
            {/* <Kefu>客服</Kefu> */}
            <Container>
                <Modal
                    visible={BDModal}
                    transparent
                    maskClosable={false}
                    title="确认报单数量" 
                    footer={[
                        { text: '取消', onPress: () => { changeBDModal(false) } },
                        { text: '确认', onPress: () => { handleApply(isCancel, BDNum) } },
                    ]}
                >
                    <div>
                        <div style={{paddingBottom: '0.5rem'}}>最大报单数量{maxBDNum}</div>
                        <Stepper
                            style={{ minWidth: '100px' }}
                            showNumber
                            max={maxBDNum}
                            min={1}
                            value = {BDNum}
                            onChange = {chnageBDNum}
                        />
                    </div>
                </Modal>
                <input value={WeiXinService} name="fileupload" onChange={() => { }} ref={(input) => { this.copyInput = input }}
                 style={{ width: 1, height: 1, overflow: "hidden", zIndex: -1, opacity: 0, padding: 0, margin: 0, border: "none", position: "absolute", top: 0, left: 0 }}></input>
                <MyWarp>
                        <MyTop>
                            <TopWarp>
                                <TopWarpLeft>
                                    <TopImage src={'/imgs/head.png'}></TopImage>
                                    <UserMessage>
                                        <UserPhone>账号: {Message.Account}</UserPhone>
                                            <UserId>{Message.RealName}</UserId>
                                    </UserMessage>
                                </TopWarpLeft>
                                {/* <UserLevel>今日任务总数: { Message.TotalTask2 ? (Message.TotalTask2) : 0 }</UserLevel> */}

                                <ProgressWarp className='exdent'>
                                    <div className='commision'>
                                        <div>溪币总计：<span className='num'>{Message.pledgeMoney}</span>溪币</div>
                                        <div className='line'>|</div>
                                        <div onClick={() => { history.push('/usermoney') }} >账户余额：<span className='num'>{Message.CreditMoney}</span>溪币</div>
                                        <div className='line'>|</div>
                                        <div onClick={() => { history.push('/tixian') }}>提现</div>
                                    </div>
                                </ProgressWarp>

                            </TopWarp>
                        </MyTop>
                        <MyList>
                            <DvaCell cellStyle={{ marginTop: 0 }} leftIcon = "&#xe645;"  IconStyle = { { fontSize: 25, top: 4.5  , width: 25 , height: 25 ,lineHight: 25} } title ="账号信息"  textVal = {0} rightIcon= "&#xe6ab;"  handleCell ={() =>{ handleUser(history) }} ></DvaCell>

                            <DvaCell cellStyle={{ marginTop: 1 }} leftIcon="&#xe725;" title="开启短信通知" textVal={Message.IsSMSNotice ? <span style={{ color: '#4dd865' }}>开启</span> : '关闭'} rightIcon={
                                <Switch
                                    className='task-switch'
                                    checked={Message.IsSMSNotice === 1 ? true : false}
                                    onChange={() => {
                                        alert('提示', `确定确认${Message.IsSMSNotice === 1 ? '关闭' : '开启'}???`, [
                                            { text: '否', onPress: () => console.log() },
                                            { text: '是', onPress: () => { handleNotice(Message.IsSMSNotice, Message) } },
                                        ])
                                    }}
                                />
                            } NumberStyle={{ fontSize: 11, color: "#dda535" }} handleCell={handleNone} ></DvaCell>

                            <DvaCell cellStyle={{ marginTop: 1 }} IconStyle={{fontSize: 24}} leftIcon = "&#xe75e;"  title ="我的旺旺号"  textVal = {0} rightIcon= "&#xe6ab;"  handleCell ={() => { history.push('/addWw') }}></DvaCell>

                            <DvaCell cellStyle={{ marginTop: 1 }} IconStyle={{fontSize: 24}} leftIcon = "&#xe78b;"  title ="账户明细"  textVal = {0} rightIcon= "&#xe6ab;"  handleCell ={() => { history.push('/usermoney') }}></DvaCell>
                            
                            <DvaCell cellStyle={{ marginTop: 1 }} IconStyle={{fontSize: 24}} leftIcon = "&#xe78b;"  title ="提现明细"  textVal = {0} rightIcon= "&#xe6ab;"  handleCell ={() => { history.push('/tixianinfo') }}></DvaCell>

                            {
                                (Message.IsYWY == 1 || Message.IsYWY == 2) ? (<>
                                    {
                                        Message.CanInviteTimes > 0 ? (<DvaCell cellStyle={{ marginTop: 1 }} leftIcon = "&#xeb7c;"  title ="推荐好友"  textVal = {0} rightIcon= "&#xe6ab;"  handleCell ={() => { history.push('/invite') }}></DvaCell>) : null
                                    }
                                </>) : null
                            }

                            {
                               (Message.IsYWY == 1 || Message.IsYWY == 2) ? (<DvaCell cellStyle={{ marginTop: 1 }} leftIcon = "&#xe693;"  title ="我的推荐"  textVal = {0} rightIcon= "&#xe6ab;"  handleCell ={() => {handleExt(history)}}></DvaCell>) : null
                            }

                            <DvaCell cellStyle={{ marginTop: 1 }} leftIcon = "&#xe620;" IconStyle={{fontSize: 24}}  title ="联系客服"  rightIcon= "&#xe6ab;" NumberStyle={{ color: '#33a3f4' }} handleCell ={() => { handleKF(Message.UserId, Message.Account, Message.RealName) }}></DvaCell>

                            <DvaCell cellStyle={{ marginTop: 1 }} leftIcon = "&#xe75b;"  title ="常见问题"  NumberStyle={{ color: '#33a3f4' }} rightIcon= "&#xe6ab;" handleCell ={() => { history.push('/help') }}></DvaCell>

                            {/* <DvaCell cellStyle={{ marginTop: 1 }} leftIcon="&#xe725;" title="申请报单" textVal={`当前报单数量 ${centerBDNum}`} rightIcon={
                                isCancel ? (<button className="modal-btn"
                                    onClick={() =>
                                        alert('提示', '确定取消报单???', [
                                            { text: '否', onPress: () => console.log() },
                                            { text: '是', onPress: () => { handleApply(isCancel) } },
                                        ])
                                    }
                                >取消报单</button>) : (<button className="modal-btn active" onClick={() => { changeBDModal(true) }}>报单</button>)
                            } NumberStyle={{ fontSize: 11, color: "#EC503F" }} handleCell={handleNone} >    
                            </DvaCell> */}

                            {/* {
                                USER_TYPE.userType == USER_TYPE.type_extend ? (null) : (USER_TYPE.userType == USER_TYPE.type_unextend ? (
                                    <DvaCell cellStyle={{ marginTop: 1 }} leftIcon = "&#xe71a;"  title ="我的礼品"  textVal = {0} rightIcon= "&#xe6ab;"  handleCell ={() => {handleGift(history)}}></DvaCell>
                                ) : (null))
                            } */}

                            {/* <DvaCell cellStyle={{ marginTop: 15 }} leftIcon = "&#xe71f;" IconStyle = { { fontSize: 24, top: 5  , width: 24 , height: 24 ,lineHight: 24} } title ="版本信息"  textVal = {'v2.0.0'} rightIcon= "&#xe6ab;"  handleCell ={handleNone}></DvaCell> */}

                            <OutLogin onClick = {showAlert}>退出登录</OutLogin>

                        </MyList>
                    </MyWarp>      
            </Container>
        </Fragment>)
    }
}
 
const mapStateToProps = (state)=>{
    return {
        isCancel: state.getIn(['my','isCancel']),
        JFList: state.getIn(['video', 'JFList']),
        JFPage: state.getIn(['video', 'JFPage']),
        maxBDNum: state.getIn(['my', 'maxBDNum']),
        BDModal: state.getIn(['my', 'BDModal']),
        BDNum: state.getIn(['my', 'BDNum']),
        centerBDNum: state.getIn(['my', 'centerBDNum']),
        WeiXinService: state.getIn(['my', 'WeiXinService']),
        newwxList: state.getIn(['my', 'wxList']),
        newUserInfo: state.getIn(['home','userInfo']),
    }
}

const mapDispatchToProps = (dispatch)=>{
    return {
        handleCopy(input, val) {
            dispatch(actionCreatore.setWeiXinService(val))
            
            setTimeout(()=>{
                Toast.info("微信号已复制至剪贴板", 2, null, false)
                console.log(input)
                input.select()
                document.execCommand("copy")
            }, 100)
        },
        handleNotice(val, userInfo) {
            // console.log(val)
            dispatch(actionCreatore.handleNotice(val, userInfo))
        },
        handleNone(){

        },
        handleBDoK(value) {
            console.log(`确认报单数量${value}`)
        },
        chnageBDNum(value) {
            dispatch(actionCreatore.changeBDNum(value))
        },
        changeBDModal(value) {
            dispatch(actionCreatore.changeBDModal(value))
        },
        handleGift(history) {
            history.push('/giftList')
        },
        handleUser(history) {
            history.push('/userInfor')
        },
        handleInital(){
            dispatch(actionCreatore.handleInital())
        },
        handleKF(id, account, name) {
            // console.log(Message)
            // return
            dispatch(actionCreatore.handleKF(id, account, name))
        },
        handleExt(history){
            history.push('/ext')
        },
        handleOut(history){
            localStorage.removeItem('user_token')
            localStorage.removeItem('user_info')
            history.push('/login')
        },

        handleApply(isCancel, BDNum){
            console.log(isCancel, BDNum)
            // return
            dispatch(actionCreatore.changeApply(isCancel, BDNum))
        }
        
    }   
}
 
export default connect(mapStateToProps, mapDispatchToProps)(My);