import React, { Fragment, useState, useEffect, useRef } from 'react';
import { ContenWarp, SearchWarp, TermTime } from './style'
import './style.css'
import { DvaHeader } from '../../../../common/Template'
import { WhiteSpace, Button, Modal, Toast, Tabs, DatePicker, List } from 'antd-mobile';
import { Link, useHistory } from 'react-router-dom'
import { Axios } from '../../../../common/Axios'
import { Empty } from 'antd';
import { myImgPath, defaultImg } from '../../../../common/Js/orderFiltter'
import { shopFilter, videoStatus } from '../../../../common/Js/textFilter'

const Tixian = (props) => {
    const alert = Modal.alert;
    let history = useHistory();
    const myInput = useRef(null)
    const [list, setList] = useState([])
    const [allList, setAllList] = useState([])
    const [maxTime, setMaxTime] = useState(null)
    const [minTime, setMinTime] = useState(null)
    const [time, setTime] = useState(null)
    const [TremIndex, setTremIndex] = useState(0)
    const [searchValue, setSearchValue] = useState('')
    const [errMsg, seterrMsg] = useState('')

    useEffect(() => {
        // getUserInfo((data)=>{
        //     // console.log(data)
        // })
        setTime(new Date(Date.now()))
        setMaxTime(new Date(Date.now()))
        setMinTime(new Date(Date.now() - 1000*60*60*24*6))

        getTaskList(new Date(Date.now()), (res) => {
            // console.log(res.type === -1)
            if(res.type == -1) {
                seterrMsg(res.message)
            }
            setList(res.resultdata.list)
            setAllList(res.resultdata.list)
        })
    }, [])

    const getUserInfo = (callback) => {
        Axios('get', '/GetBrusherInfo', {}).then(data => {
            localStorage.setItem('user_info', JSON.stringify(data.resultdata))
            callback(data.resultdata)
        }).catch(err => {

        })
    }

    const getTaskList = (date , callback) => {
        Axios('get', '/GetTaskList', {
            CurrentTabIndex: 0,
            IsNoPic: 0,
            ChangeDate: date.getFullYear() + '/' + (date.getMonth() + 1) + '/' + date.getDate(),
            SearchAccount: '',
            nowPage: 1,
            Status: 0
        }).then(data => {
            callback(data)
        }).catch(err => {

        })
    }

    const changeTab = (index, tab) => {
        console.log(index, tab)
        if (index === 0) {
            fillterFn(0)
        }
        switch (index) {
            case 0:
                fillterFn(0)
                break;
            case 1:
                fillterFn(1)
                break;
            case 2:
                fillterFn(2)
                break;
            case 3:
                fillterFn(3)
                break;
            case 4:
                fillterFn(4)
                break;

            default:
                break;
        }
    }

    const fillterFn = (int) => {
        let waitList = []
        let backList = []
        let gobackList = []
        let errList = []
        for (let i = 0; i < allList.length; i++) {
            const el = allList[i];
            if (el.Status === 'WaitOper') {  // 待操作
                waitList.push(el)
            } else if (el.Status == 'OK' && el.IsReturnCapital == 0) {  //已返还
                backList.push(el)
            } else if (el.IsReturnCapital == 1) {  //待返还
                gobackList.push(el)
            } else if (el.Status == 'TimeOut' || el.Status == 'UserCancle') {   //已失效
                errList.push(el)
            }
        }
        if (int === 0) {
            setList(allList)
        } else if (int === 1) {
            setList(waitList)
        } else if (int === 2) {
            setList(backList)
        } else if (int === 3) {
            setList(gobackList)
        } else if (int === 4) {
            setList(errList)
        }
    }

    const tabs = [
        { title: '全部', value: 'all' },
        { title: '待操作', value: 'WaitOper' },
        { title: '待返还', value: 'OK' },
        { title: '已返还', value: 'IsReturnCapital' },
        { title: '已失效', value: 'TimeOut' },
    ]

    const changeTime = (date, val, index) => {
        console.log(date, val, index)
        Toast.loading('正在加载数据中...', 0, null, true)
        getTaskList(date, (res) => {
            Toast.hide()
            if(res.type === -1) {
                seterrMsg(res.message)
            }
            setList(res.resultdata.list)
            setAllList(res.resultdata.list)
            setTime(date)
        })
    }

    return (<Fragment>
        <ContenWarp>
            <SearchWarp>
                <TermTime>
                    <DatePicker  
                        maxDate={maxTime}
                        mode="date"
                        minDate={minTime}
                        title="选择日期"
                        extra="请选择"
                        value={time}
                        onChange={(date) => { changeTime(date, searchValue, TremIndex) }}
                    >
                        <List.Item arrow="horizontal">任务日期</List.Item>
                    </DatePicker>
                </TermTime>
            </SearchWarp>
            <Tabs tabs={tabs}
                initialPage={0}
                onChange={(tab, index) => { changeTab(index, tab); }}
                tabBarActiveTextColor={'#ec503f'}
                tabBarUnderlineStyle={{ border: '1px #ec503f solid' }}
            >
                <div className='list-warp'>
                    {
                        list.length ? (<>
                            {
                                list.map((item, index) => {
                                    return (<div key={index} className='list-item'>
                                        <div style={{ paddingBottom: 5, borderBottom: '1px solid #eee' }}>
                                            <div className='type'>{
                                                videoStatus(item)
                                            }</div>
                                   &nbsp;&nbsp;
                                   {
                                                item.IsReturnCapital == 1 ? ('返款时间:' + item.ReturnTime) : null
                                            }
                                            {
                                                item.Status == 'Dispose' ? (<>被释放原因:
                                        {
                                                        item.Description ? item.Description : '--'
                                                    }
                                                </>) : null
                                            }
                                        </div>
                                        {/* <div className='title'>
                                        <div className='renwu'>垫付任务</div>
                                   </div> */}
                                        <div className='good-info'>
                                            <div className='img-warp'>
                                                <img src={item.ImgPath ? myImgPath(item.ImgPath) : defaultImg} alt="" />
                                            </div>
                                            <div className='good-desc'>
                                                <div className='good-title'>  店铺：
                                                {
                                                        item.Status === 'OK' ? (item.ShopName) : (shopFilter(item.ShopName))
                                                    }
                                                </div>
                                                <div className='good-text'>
                                                    <span style={{ marginRight: '8px' }}>应付:</span>
                                                    <span>{item.NeedPayMoney}</span>
                                                </div>
                                                <div className='good-text' style={{ marginTop: 2 }}>
                                                    <span style={{ marginRight: '8px' }}>实付:</span>
                                                    <span>{item.TaskSelfMoney}</span>
                                                </div>

                                                <div className='good-text' style={{ marginTop: 2 }}>
                                                    <span style={{ padding: '5px', lineHeight: '1.5', background: '#d3b3c3', borderRadius: 5, marginRight: '8px' }}>做单旺旺:</span>
                                                    <span>{item.BrusherAliAccount}</span>
                                                </div>
                                                <br />
                                                <div className='good-time'>接单时间:    <br /> {item.CreateDate}</div>
                                            </div>
                                        </div>


                                    </div>)
                                })
                            }
                        </>) : ( errMsg ? <div className='err-msg'>{errMsg}</div> : <Empty image={Empty.PRESENTED_IMAGE_SIMPLE} description={'暂无数据'} />)
                    }
                </div>
            </Tabs>
        </ContenWarp>
    </Fragment>)
}

export default Tixian